@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* For mobile phones: */
header {
    font-size: 1.2rem;
    color: var(--main-light-text-color);
    background-color: var(--main-accent-bg-color);
    width: 100vw;
    padding: var(--main-standard-text-padding) 0 var(--main-standard-text-padding) 0;

}

header .menuHamberger {
    height: 30px;
    padding: 0 15px 0 15px;
    vertical-align: middle;
}

#headerContainer {
    display: flex;
    justify-content: space-between;
}

#userName {
    margin-left: 10px;
}

#headerMenu {
    display: flex;
    justify-content: flex-end;
}

.headerItem {
    margin: auto 10px;
    color: var(--main-light-text-color);
}

.headerItem a {
    text-decoration: none;
}


.headerItem:hover {
    /* background-color: var(--main-accent-bg-color-hover); */
    cursor: pointer;
}

#bug_list_view_table {
    text-align: center;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
}

#bug_list_view_table td,
#bug_list_view_table th {
    border: 1px solid #ddd;
    padding: 5px;
    cursor: default;
}

#bug_list_view_table tr:nth-child(even) {
    background-color: #f2f2f2;
}

#bug_list_view_table tr:hover {
    background-color: #ddd;
}

#bug_list_view_table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4caf50;
    color: white;
    cursor: pointer;
}

.bug_list_view_check_box {
    cursor: pointer;
}


