#CreateBugForm {
    width: 90vw;
    margin: auto;
}

#CreateBugForm label{
    margin: auto 10px;
}

#CreateBugForm input#bugTitle {
    width: 90vw;
}

#CreateBugForm textarea#bugDescription {
    width: 40vw;
    height: 4rem;
}

#CreateBugForm input#bugDescription {
    width: 40vw;
}
/* Global Custom Properties */
:root {
  --main-bg-color: rgb(241, 241, 241);
  --main-accent-bg-color: rgb(45, 111, 211);
  --main-accent-bg-color-hover: rgb(65, 125, 214);
  --main-header-color: #4caf50;
  --main-light-text-color: #fff;
  --main-dark-text-color: rgb(48, 48, 48);
  --main-highlight-text-color: yellow;
  --main-error-text-color: red;

  --main-standard-text-padding: 10px;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.flex-even {
  display: flex;
  justify-content: space-evenly;
}

.flex-right {
  display: flex;
  justify-content: flex-end;
}

.dividing_line_main {
  border-bottom: 1px solid rgb(48, 48, 48);
  border-bottom: 1px solid var(--main-dark-text-color);
  padding: 3px 0;
}

.dividing_line_secondary {
  border-bottom: 1px dotted rgb(88, 88, 88);
  padding: 3px 0;
}

.error {
  color: red;
  color: var(--main-error-text-color);
}

.bold-font {
  font-weight: 700;
}

.btn {
  min-width: 15vw;
  max-width: 20vw;
  font-size: 1.4rem;
  padding: 5px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: rgb(48, 48, 48);
  color: var(--main-dark-text-color);
}

/* For mobile phones: */


#app {
  height: 100vh;
  background-color: rgb(241, 241, 241);
  background-color: var(--main-bg-color);
  position: relative;
}
